.blog-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background: linear-gradient(135deg, #0f0f23 0%, #1a1a2e 100%);
  min-height: 100vh;
  color: #ffffff;
}

.blog-header {
  margin-bottom: 2rem;
}

.back-button {
  display: inline-flex;
  align-items: center;
  padding: 0.75rem 1.5rem;
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  text-decoration: none;
  border-radius: 8px;
  font-weight: 500;
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.2);
  margin-bottom: 1.5rem;
}

.back-button:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
  color: #ffffff;
  text-decoration: none;
}

.blog-container h1 {
  font-size: 2.5rem;
  color: #ffffff;
  margin-bottom: 2rem;
  line-height: 1.2;
}

.blog-content {
  line-height: 1.6;
  color: #d0d0d0;
}

.blog-content h2 {
  font-size: 1.8rem;
  color: #ffffff;
  margin: 2rem 0 1rem;
}

.blog-content h3 {
  font-size: 1.4rem;
  color: #ffffff;
  margin: 1.5rem 0 1rem;
}

.blog-content p {
  margin-bottom: 1.5rem;
}

.blog-content ul {
  margin-bottom: 1.5rem;
  padding-left: 2rem;
}

.blog-content li {
  margin-bottom: 0.5rem;
}

.blog-content strong {
  color: #ffffff;
}

@media (max-width: 768px) {
  .blog-container {
    padding: 1rem;
  }

  .blog-container h1 {
    font-size: 2rem;
  }

  .blog-content h2 {
    font-size: 1.5rem;
  }

  .blog-content h3 {
    font-size: 1.2rem;
  }
} 